<!--每日信访动态-->
<template>
  <div class="wrapper">
    <div class="content">
      <div class="w-center">
        <div class="part part1">
          <ctitle :title="`${curCity==='广东省'?'全省':curCity}信访量`"></ctitle>
          <div class="detail">
            <p class="allNumber">
            <span class="counts">
              <incNumber class="number" :incNumber="jctotal"></incNumber>
            </span>件次
            </p>
            <p v-for="(item,index) of jcType" :key="index">
              {{item.name}}
              <span class="counts">
              <incNumber class="number" :incNumber="item.value"></incNumber>
            </span>
            </p>
          </div>
          <div class="charts">
            <div v-if="mapParams.options.JSON!==cookieCity" class="map-back" @click="mapBack"></div>
            <transition name="fade">
              <Map v-if="mapFlag" :params="mapParams" @change="handleMap"></Map>
            </transition>
          </div>
        </div>
        <div class="part part2">
          <ctitle :title="'区域信访量排行'"></ctitle>
          <div class="charts">
            <barline :params="xflphBarParams"></barline>
          </div>
        </div>
      </div>
      <div class="w-right">
        <div class="part part1">
          <ctitle :title="'热点问题TOP10'"></ctitle>
          <div class="charts">
            <barline :params="rdwtBarParams"></barline>
          </div>
        </div>
        <div class="part part2">
          <ctitle :title="'信访量概况'"></ctitle>
          <div class="charts">
            <div class="xfl-list"
                 v-loading="!xflList.data.length"
                 :element-loading-background="$STANDARD.loadingBgColor">
              <ul v-if="xflList.data.length">
                <li
                  class="item"
                  v-for="(item,index) of xflList.data"
                  :key="index"
                  :style="xflList.pos[index]"
                >
                  <p class="name"
                     :style="{lineHeight:item.name.length>2?'20px':'40px'}">
                    {{item.name}}</p>
                  <p class="value">{{item.value}}</p>
                  <p class="unit">件次</p>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import Cookies from 'js-cookie'
  import {get_http} from "@/api/index";
  import ctitle from "@/components/title/index";
  import incNumber from "@/components/incNumber/index";
  import Map from "@/components/map/Map";
  import barline from "@/components/bar/BarLine";

  export default {
    name: "index",
    components: {
      ctitle,
      incNumber,
      Map,
      barline
    },
    data() {
      return {
        curCity: "广东省",
        shijCity: "",
        shej_02: "invalid",
        shij_02: "invalid",
        xj_02: "invalid",
        nameP: "",
        mapJson: {
          "广东省": "440000",
          "广州市": "440100",
          "韶关市": "440200",
          "深圳市": "440300",
          "珠海市": "440400",
          "汕头市": "440500",
          "佛山市": "440600",
          "江门市": "440700",
          "湛江市": "440800",
          "茂名市": "440900",
          "肇庆市": "441200",
          "惠州市": "441300",
          "梅州市": "441400",
          "汕尾市": "441500",
          "河源市": "441600",
          "阳江市": "441700",
          "清远市": "441800",
          "东莞市": "441900",
          "中山市": "442000",
          "潮州市": "445100",
          "揭阳市": "445200",
          "云浮市": "445300",
        },
        //1.地图
        mapFlag: false,
        mapParams: {
          canvasStyle: {
            width: "100%",
            height: "100%",
          },
          data: [],
          options: {
            isHome: true,
            JSON: "440000",
            xName: "name",
            yName: "value_g0_1",
            map: {
              "value_g0_1": "总数",
              "value_g1_1": "来信",
              "value_g1_2": "来访",
              "value_g1_3": "网上投诉"
            },
            carousel: true
          }
        },
        //2.地图左上角(件次总数)
        jctotal: 0,
        //3.地图左上角(各类件次)
        jcType: [],
        //4.区域信访量排行
        xflphBarParams: {
          data: [],
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          options: {
            // isVertical:true,
            xName: "name", //x轴字段
            axisValueShow: false,
            categoryBr: true,
            axis1Name: "人次",
            axisLabelSize: 14, // label字号
            left: "0%",
            right: "5%",
            top: "0%",
            bottom: "0%",
            barLabelOptions: "top",
            barWidth: "50%",
            barBorderRadius: 5,
            splitLineShow: false,
            barLabelShow: false, //柱状图数量值显示
            axisLabelColor: "rgba(0, 176, 255, .8)",
            itemLabelColor: "rgba(0, 176, 255, .8)",
            colors: {
              bar: [["#fa71cd", "#c471f5"]]
            },
            map: {
              value: "数量"
            }
          }
        },
        //5.热点问题分布
        rdwtBarParams: {
          data: [],
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          options: {
            // isVertical:true,
            xName: "name", //x轴字段
            axisValueShow: false,
            categoryBr: true,
            axis1Name: "人次",
            axisLabelSize: 14, // label字号
            left: "0%",
            right: "5%",
            top: "0%",
            bottom: "5%",
            barLabelOptions: "top",
            barWidth: "50%",
            barBorderRadius: 5,
            splitLineShow: false,
            barLabelShow: false, //柱状图数量值显示
            axisLabelColor: "rgba(0, 176, 255, .8)",
            itemLabelColor: "rgba(0, 176, 255, .8)",
            colors: {
              bar: [["#7061ff", "#5cd9ff"]]
            },
            map: {
              value: "数量"
            }
          }
        },
        //6.信访量概况
        xflList: {
          data: [],
          pos: [
            {
              top: '50px',
              left: '270px'
            },
            {
              top: '170px',
              left: '100px'
            },
            {
              top: '170px',
              left: '440px'
            },
          ]
        }
      }
    },
    mounted() {

      for (let key in this.mapJson) {
        if (this.mapJson[key] === this.cookieCity) {
          this.curCity = key
        }
      }
      this.mapParams.options.JSON = this.cookieCity;
      this.getMapAPI();
    },
    methods: {
      // 1.全省信访量-地图
      getMapAPI() {
        // 处理接口参数
        // 省级
        // if (this.curCity === '广东省') {
        //   this.shej_02 = this.curCity;
        //   this.shij_02 = 'invalid';
        //   this.xj_02 = 'invalid';
        //   this.nameP = 'shij_02';
        // } else {
        //   // 市级
        //   if (this.shijCity) {
        //     this.shej_02 = '广东省';
        //     this.shij_02 = this.shijCity;
        //     this.xj_02 = 'invalid';
        //     this.nameP = 'xj_02';
        //   } else {
        //     // 县区级
        //     this.shej_02 = '广东省';
        //     this.shij_02 = this.shijCity;
        //     this.xj_02 = this.curCity;
        //     this.nameP = 'invalid';
        //   }
        // }
        get_http("mrxfdtMap", {
          Cqh: this.curCity
        })
          .then(res => {
            this.mapParams.options.map = res.data.map;
            this.mapParams.data = res.data.data;
            this.mapFlag = true;
            this.getAPI()
            this.getPHAPI();
          })
          .catch(err => {
            console.log(err);
          });
      },
      getAPI() {
        // 2.全省信访量-件次总数（地图左上角）
        get_http("mrxfdtJctotal", {
          Cqh: this.curCity
        })
          .then(res => {
            this.jctotal = res.data.data[0].value;
          })
          .catch(err => {
            console.log(err);
          });
        // 3.全省信访量-各类件次（地图左上角）
        get_http("mrxfdtJcType", {
          Cqh: this.curCity
        })
          .then(res => {
            this.jcType = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        
        // 5.热点问题分布
        get_http("mrxfdtRdwtBar", {
          Cqh: this.curCity
        })
          .then(res => {
            this.rdwtBarParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 6.信访量概况
        get_http("mrxfdtXflList", {
          Cqh: this.curCity
        })
          .then(res => {
            this.xflList.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
      },
      getPHAPI() {
        // 4.区域信访量排行
        get_http("mrxfdtXflphBar", {
          Cqh: this.curCity
        })
          .then(res => {
            this.xflphBarParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
      },
      // 点击地图下钻
      handleMap(name) {
        this.curCity = name;
        for (let key in this.mapJson) {
          if (this.curCity === key) {
            this.mapFlag = false;
            this.mapParams.options.JSON = this.mapJson[name];
          }
        }
      },
      // 地图返回全省
      mapBack() {
        this.mapFlag = false;
        this.curCity = "广东省";
        this.mapParams.options.JSON = "440000";
      },
    },
    computed: {
      cookieCity() {
        return Cookies.get('xzqhdm')
      }
    },
    watch: {
      curCity: function (newVal, oldVal) {
        if (newVal === '广东省') {
          this.getMapAPI();
        } else {
          let c = '';
          for (let key in this.mapJson) {
            if (newVal === key) {
              this.shijCity = newVal;
              c = newVal;
              this.getMapAPI();
            }
          }
          if (!c) {
            this.xj_02 = newVal;
            this.getAPI()
          }
        }
      }
    }
  }
</script>

<style lang="stylus" scoped>
  .wrapper
    .content
      width 100%
      height 100%
      display flex
      padding .2rem .3rem 0 .3rem
      box-sizing border-box

      .charts
        position relative
        width 100%
        height calc(100% - 0.28rem)
        box-sizing border-box

      .w-center
        flex 1.5
        width 100%
        height 100%
        display flex
        flex-direction column

        .part1
          flex 4
          width 100%
          height 100%
          position relative

          .fade-enter-active, .fade-leave-active
            transform scale(1)
            transition all 0.8s

          .fade-enter, .fade-leave-to
            opacity 0
            transform scale(0)

          .map-back
            position absolute
            top 30px
            right 60px
            z-index 1000
            width 30px
            height 30px
            background url("~@/assets/back-icon.png") no-repeat
            cursor pointer

          .detail
            position absolute
            top 0.5rem
            left 0.4rem

            .allNumber
              font-size 0.2rem
              color #5aceff
              margin-bottom 0.2rem

              .bgNumber
                display inline-block

                ul
                  li
                    float left
                    width 30px
                    line-height 50px
                    margin 0 2px
                    font-size 40px
                    font-weight 700
                    color #fff
                    border 2px solid #02adf5
                    border-radius 6px
                    text-align center
                    text-shadow 0 0 6px #276bf4, 0 0 10px #276bf4, 0 0 20px #276bf4, 0 0 30px #276bf4, 0 0 40px #276bf4
                    box-shadow 0 0 10px #276bf4

              .number
                font-size 0.5rem
                color #fff
                text-shadow 0 0 6px #276bf4, 0 0 10px #276bf4, 0 0 20px #276bf4, 0 0 30px #276bf4, 0 0 40px #276bf4
                margin-right 0.15rem

            p + p
              line-height 0.4rem
              color #ffffff
              font-size 0.16rem

              .number
                font-size 0.22rem
                color #128f9e
                margin-left 0.15rem
                vertical-align -0.04rem
                font-weight 700

        .part2
          flex 1

      .w-right
        flex 1
        height 100%
        display flex
        flex-direction column

        .part
          padding-bottom .1rem

        .part1
          flex 1.3

          .charts
            background url("~@/assets/part-bg-s.png") no-repeat
            background-size 100% 100%

        .part2
          flex 1

          .charts
            background url("~@/assets/part-bg-b.png") no-repeat
            background-size 100% 100%
          .xfl-list
            width 100%
            position relative
            li.item
              position absolute
              width 189px
              height 106px
              background url("../../assets/blfs-icon2.png") no-repeat
              .name
                position absolute
                top 35px
                left 32px
                width 50px
                font-size 16px
                color #fff
                text-align center
                cursor pointer
              .active
                text-shadow: 0 0 6px #276bf4, 0 0 10px #276bf4, 0 0 20px #276bf4, 0 0 30px #276bf4, 0 0 40px #276bf4;
              .value
                position absolute
                top 45px
                right 15px
                font-size 16px
                color #ffde00
                font-weight 700
              .unit
                position absolute
                bottom 22px
                right 15px
                font-size 14px
                color #12dded
</style>
